                <el-table-column prop="" label="员工姓名" width="" align="left"></el-table-column>
                <el-table-column prop="" label="基本工资" width="" align="left"></el-table-column>
                <el-table-column prop="" label="奖金" width="" align="left"></el-table-column>
                <el-table-column prop="" label="津贴" width="" align="left"></el-table-column>
                <el-table-column prop="" label="加班" width="" align="left"></el-table-column>
